.header-color{
  background: #00b09b;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #96c93d, #00b09b);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #96c93d, #00b09b); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid{
  background: #00b09b;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #96c93d, #00b09b);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #96c93d, #00b09b); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
   text-align: center;
  position: relative;
   width: 100%;
   height: 100%;
   left: 0;
   right: 0;
  
}

.avatar-img{
  height: 250px;
  padding-top:5es;
}

.banner-text {
  background-color: black;
  opacity:  0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
}
.banner-text hr{
 border-top: 5px dotted white;
 width: 50%;
 margin:auto;
}

.banner-text p{
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i{
  color: white;
  padding: 8px;
  font-size: 5em;
}
.contact-body{
  margin: auto;
  background: grey;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
 text-align: center;
 justify-content: center;
 width: 80%;
 height: 550px;
 background: white;
 color: black;
 border: 2px solid red;
 margin-bottom: 20px;
}
.contact-grid p{
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}
.contact-grid h2{
  font-family: 'Anton',sans-serif;
}

.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}